@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Regular.woff2") format("woff2"), url("../fonts/Poppins-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Medium.woff2") format("woff2"), url("../fonts/Poppins-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Bold.woff2") format("woff2"), url("../fonts/Poppins-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "CormorantGaramond";
  src: url("../fonts/CormorantGaramond-Regular.woff2") format("woff2"), url("../fonts/CormorantGaramond-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "CormorantGaramond";
  src: url("../fonts/CormorantGaramond-SemiBold.woff2") format("woff2"), url("../fonts/CormorantGaramond-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "CormorantGaramond";
  src: url("../fonts/CormorantGaramond-Bold.woff2") format("woff2"), url("../fonts/CormorantGaramond-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  scroll-behavior: smooth;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "CormorantGaramond", "Arial", sans-serif;
  margin: 0;
}

figure {
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

button {
  background: transparent;
  border: none;
  padding: 0;
}

.body {
  height: 100%;
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20.8px;
  font-weight: 400;
  font-style: normal;
  display: flex;
  flex-flow: column;
  color: #1D1613;
  margin: 0 auto;
}

.main {
  flex: 1 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}

a {
  text-decoration: none;
  color: #1D1613;
}

img {
  border-style: none;
}

p {
  margin: 0;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

.hide {
  display: none;
}

.show {
  display: block;
}

.header {
  width: 100%;
  max-width: 1366px;
  position: relative;
  padding: 30px 19px 19px;
  margin: 0 auto;
  box-sizing: border-box;
}
.header::before {
  content: "";
  width: 100%;
  height: 1px;
  position: absolute;
  top: 114px;
  left: 0;
  background: #F6F5F4;
}
@media (max-width: 1023px) {
  .header {
    padding: 30px 45px;
  }
  .header::before {
    display: none;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 20px 15px;
  }
}

.header nav {
  width: calc(100% - 157px);
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .header nav {
    width: 100%;
    justify-content: space-between;
    margin: 0;
  }
}

.header__button-menu {
  display: none;
}
@media (max-width: 1023px) {
  .header__button-menu {
    height: max-content;
    display: initial;
    order: 1;
    margin: 24px 0 0;
  }
}
@media (max-width: 767px) {
  .header__button-menu {
    margin: 17px 0 0;
  }
}

@media (max-width: 1023px) {
  .header__button-menu.no-js {
    margin: 25px 0 0;
  }
}
@media (max-width: 767px) {
  .header__button-menu.no-js {
    margin: 18px 0 0;
  }
}

@media (max-width: 1023px) {
  .header__button-menu.no-js path {
    stroke: #ffffff;
  }
}

.header__button-search-container {
  width: 10.2%;
  position: relative;
  padding: 12px 0 0 29px;
}
@media (max-width: 1023px) {
  .header__button-search-container {
    width: 100%;
    display: none;
    order: 4;
    border-radius: 21px;
    padding: 0;
    margin: 41px 0 0;
  }
}
@media (max-width: 767px) {
  .header__button-search-container {
    margin: 31px 0 0;
  }
}

@media (max-width: 1023px) {
  .header__button-search-container.no-js {
    display: initial;
  }
}

.header__button-search-container input {
  font-family: inherit;
  font-size: 14px;
  line-height: 21px;
  font-weight: 500;
  border: none;
  padding: 0;
  cursor: pointer;
}
@media (max-width: 1023px) {
  .header__button-search-container input {
    width: 100%;
    background: rgba(255, 255, 255, 0.15);
    border-radius: 21px;
    padding: 8px 0 11px 40px;
  }
}

.header__button-search-container svg {
  width: 14px;
  height: 14px;
  position: absolute;
  top: 15px;
  left: 0;
  fill: #000000;
}
@media (max-width: 1023px) {
  .header__button-search-container svg {
    top: 13px;
    left: 13px;
  }
}
@media (max-width: 767px) {
  .header__button-search-container svg {
    left: 12px;
  }
}

@media (max-width: 1023px) {
  .header__button-search-container.no-js path {
    fill: #ffffff;
  }
}

.header__logo {
  width: 66px;
  height: 66px;
}
@media (max-width: 1023px) {
  .header__logo {
    order: 2;
  }
}
@media (max-width: 767px) {
  .header__logo {
    width: 52px;
    height: 52px;
  }
}

@media (max-width: 1023px) {
  .header__logo.no-js,
.header__logo.open {
    stroke: #ffffff;
  }
}

.header__auth-container {
  width: 120px;
  display: flex;
  justify-content: space-between;
  padding: 12px 0 0;
}
@media (max-width: 1023px) {
  .header__auth-container {
    width: 23px;
    height: max-content;
    order: 3;
    padding: 0;
    margin: 24px 0 0 0;
  }
}
@media (max-width: 767px) {
  .header__auth-container {
    margin: 17px 0 0;
  }
}

@media (max-width: 1023px) {
  .header__auth-container.no-js {
    margin: 25px 0 0;
  }
}
@media (max-width: 767px) {
  .header__auth-container.no-js {
    margin: 18px 0 0;
  }
}

@media (max-width: 1023px) {
  .header__auth-container-login-desktop,
.header__auth-container-cart-desktop {
    display: none;
  }
}

.header__auth-container-cart-desktop a {
  color: #1D1613;
}
.header__auth-container-cart-desktop a:visited {
  color: #1D1613;
}
.header__auth-container-cart-desktop a:active {
  color: #1D1613;
}

.header__auth-container-cart-tablet {
  display: none;
}
@media (max-width: 1023px) {
  .header__auth-container-cart-tablet {
    display: initial;
    margin: 0 0 0 4px;
  }
}

@media (max-width: 1023px) {
  .header__auth-container-cart-tablet.no-js path {
    stroke: #ffffff;
  }
}

.header__navigation-list-container {
  width: 100%;
  display: flex;
  padding: 40px 0 0;
}
@media (max-width: 1023px) {
  .header__navigation-list-container {
    display: none;
  }
}

.header__navigation-list-container.no-js {
  width: 100%;
  display: flex;
  padding: 40px 0 0;
}
@media (max-width: 1023px) {
  .header__navigation-list-container.no-js {
    flex-flow: row wrap;
    order: 5;
    padding: 46px 0 0;
  }
}
@media (max-width: 767px) {
  .header__navigation-list-container.no-js {
    padding: 36px 0 0;
  }
}

.header__navigation-list {
  width: 495px;
  font-weight: 500;
  display: flex;
  flex-flow: row wrap;
  gap: 36px;
  margin: 0 auto 0 29.4%;
}
@media (max-width: 1023px) {
  .header__navigation-list {
    width: 100%;
  }
}

.header__navigation-list a {
  color: #1D1613;
}

.header__navigation-list-dop {
  display: none;
}

.header__login {
  display: none;
}

@media (max-width: 1023px) {
  .header.no-js,
.header.open {
    background: #A87B62;
    padding: 29px 45px 74px;
  }
}
@media (max-width: 767px) {
  .header.no-js,
.header.open {
    padding: 19px 16px 44px;
  }
}

@media (max-width: 1023px) {
  .header.no-js .header__navigation-list {
    font-size: 24px;
    line-height: 36px;
    display: flex;
    flex-flow: column;
    order: 2;
    gap: 21px;
    color: #ffffff;
    margin: 0 0 41px;
  }
}
@media (max-width: 767px) {
  .header.no-js .header__navigation-list {
    margin: 0 0 30px;
  }
}

@media (max-width: 1023px) {
  .header.no-js .header__navigation-list-dop {
    width: 100%;
    font-size: 18px;
    line-height: 23.4px;
    display: flex;
    flex-flow: column;
    order: 3;
    gap: 20px;
    color: #ffffff;
    margin: 0 0 44px;
    opacity: 75%;
  }
}
@media (max-width: 767px) {
  .header.no-js .header__navigation-list-dop {
    margin: 0 0 34px;
  }
}

@media (max-width: 1023px) {
  .header.no-js .header__login {
    font-size: 18px;
    line-height: 23.4px;
    font-weight: 500;
    display: block;
    order: 4;
    color: #ffffff;
  }
}

.banner {
  min-height: 463px;
  font-family: "CormorantGaramond", "Arial", sans-serif;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  background: url("../img/index/banner-desktop.jpg") no-repeat 0 0/cover;
  margin: 0 0 120px;
}
@media (min-resolution: 2dppx) {
  .banner {
    background: url("../img/index/banner-desktop@2x.jpg") no-repeat 0 0/cover;
  }
}
@media (max-width: 1023px) {
  .banner {
    background: url("../img/index/banner-tablet.jpg") no-repeat 0 0/cover;
    margin: 0 0 72px;
  }
}
@media (max-width: 1023px) and (min-resolution: 2dppx) {
  .banner {
    background: url("../img/index/banner-tablet@2x.jpg") no-repeat 0 0/cover;
  }
}
@media (max-width: 767px) {
  .banner {
    background: url("../img/index/banner-mobile.jpg") no-repeat 0 0/cover;
    margin: 0 0 62px;
  }
}
@media (max-width: 767px) and (min-resolution: 2dppx) {
  .banner {
    background: url("../img/index/banner-mobile@2x.jpg") no-repeat 0 0/cover;
  }
}

.banner h1 {
  font-size: 45px;
  line-height: 45.5px;
  font-weight: 700;
  text-align: center;
  letter-spacing: 0.03em;
  padding: 8px 0 0;
  margin: 0;
}
@media (max-width: 767px) {
  .banner h1 {
    font-size: 38px;
    line-height: 29px;
    padding: 13px 0 0;
  }
}

.banner span {
  font-size: 34px;
  line-height: 49.4px;
  font-style: italic;
  text-align: center;
  letter-spacing: 0.01em;
  display: block;
}
@media (max-width: 767px) {
  .banner span {
    font-size: 32px;
  }
}

.slider {
  width: 100%;
  padding: 0 98px;
  margin: 0 0 110px;
}
@media (max-width: 1023px) {
  .slider {
    padding: 0 45px;
    margin: 0 0 59px;
  }
}
@media (max-width: 767px) {
  .slider {
    padding: 0 15px;
    margin: 0 0 46px;
  }
}

.swiper {
  width: 100%;
  max-width: 1170px;
  height: 583px;
}
@media (max-width: 1023px) {
  .swiper {
    height: 615px;
  }
}
@media (max-width: 767px) {
  .swiper {
    height: 409px;
  }
}

.swiper h2 {
  font-size: 34px;
  line-height: 45px;
  font-weight: 700;
  text-align: center;
}
@media (max-width: 1023px) {
  .swiper h2 {
    line-height: 40px;
  }
}
@media (max-width: 767px) {
  .swiper h2 {
    font-size: 33px;
    margin: 0 0 20px;
  }
}

.swiper__button-container {
  display: flex;
  justify-content: space-between;
  margin: 0 0 15px;
}
@media (max-width: 1023px) {
  .swiper__button-container {
    margin: 0 0 4px;
  }
}
@media (max-width: 767px) {
  .swiper__button-container {
    display: none;
  }
}

.swiper-button-prev-slide,
.swiper-button-next-slide {
  width: 29px;
  height: 9px;
  cursor: pointer;
}
@media (max-width: 1023px) {
  .swiper-button-prev-slide,
.swiper-button-next-slide {
    width: 36px;
    height: 15px;
  }
}

.swiper a {
  width: max-content;
  line-height: 20px;
  display: block;
  color: #1D1613;
  border-bottom: 1px solid #A87B62;
  margin: 0 auto 59px;
  cursor: pointer;
}
@media (max-width: 1023px) {
  .swiper a {
    line-height: 26px;
    margin: 0 auto 51px;
  }
}
@media (max-width: 767px) {
  .swiper a {
    margin: 0 auto 40px;
  }
}

.swiper-wrapper {
  width: 100%;
}

.swiper-slide img {
  width: 100%;
  max-width: 270px;
  height: auto;
  max-height: 282px;
  display: block;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .swiper-slide img {
    width: 324px;
    max-width: 324px;
    height: 340px;
    max-height: none;
  }
}
@media (max-width: 767px) {
  .swiper-slide img {
    width: 130px;
    height: 136px;
  }
}

.swiper-slide figcaption {
  font-weight: 500;
  text-align: center;
  margin: 24px 0 5px;
}
@media (max-width: 1023px) {
  .swiper-slide figcaption {
    margin: 22px 0 4px;
  }
}
@media (max-width: 767px) {
  .swiper-slide figcaption {
    margin: 16px 0 5px;
  }
}

.swiper-slide p {
  font-size: 15px;
  font-weight: 700;
  text-align: center;
}

.swiper-pagination-fraction {
  font-size: 16px;
  line-height: 20.8px;
  font-weight: 500;
}

.swiper-pagination-fraction ul {
  display: flex;
  justify-content: center;
  gap: 15px;
}
@media (max-width: 767px) {
  .swiper-pagination-fraction ul {
    gap: 10px;
  }
}

.swiper-pagination {
  color: black;
}

.swiper-pagination-bullet {
  width: 14px;
  height: 21px;
  font-family: inherit;
  font-size: 16px;
  line-height: 20.8px;
  font-weight: 500;
  color: #9B9997;
  opacity: 100%;
  border-radius: none;
  background: transparent;
}

.swiper-pagination-bullet-active {
  color: #1D1613;
}

.blog {
  max-width: 1366px;
  padding: 0 98px;
  margin: 0 auto 121px;
}
@media (max-width: 1023px) {
  .blog {
    padding: 0 45px;
    margin: 0 auto 68px;
  }
}
@media (max-width: 767px) {
  .blog {
    padding: 0 15px;
    margin: 0 auto 59px;
  }
}

.blog h2 {
  font-size: 36px;
  line-height: 34px;
  font-weight: 700;
  text-align: center;
  margin: 0 0 23px;
}
@media (max-width: 1023px) {
  .blog h2 {
    line-height: 38px;
    margin: 0 0 21px;
  }
}

.blog a {
  width: max-content;
  line-height: 26px;
  text-align: center;
  display: block;
  color: #1D1613;
  border-bottom: 1px solid #A87B62;
  margin: 0 auto 59px;
}
@media (max-width: 1023px) {
  .blog a {
    margin: 0 auto 60px;
  }
}
@media (max-width: 767px) {
  .blog a {
    margin: 0 auto 52px;
  }
}

.blog ul {
  display: grid;
  grid-auto-flow: dense;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}
@media (max-width: 1023px) {
  .blog ul {
    grid-template-columns: 1fr;
    gap: 44px 0;
  }
}
@media (max-width: 767px) {
  .blog ul {
    width: 290px;
    gap: 37px 0;
    margin: 0 auto;
  }
}

.blog li {
  width: 100%;
  max-width: 570px;
}
@media (max-width: 1023px) {
  .blog li {
    max-width: none;
  }
}

.blog li:nth-child(3n+1) {
  grid-column: 1;
}
@media (max-width: 1023px) {
  .blog li:nth-child(3n+1) {
    grid-column: auto;
  }
}

.blog li:nth-child(3n+2) {
  grid-column: 1;
}
@media (max-width: 1023px) {
  .blog li:nth-child(3n+2) {
    grid-column: auto;
  }
}

.blog li:nth-child(3n+3) {
  grid-row: span 2;
  grid-column: 2;
  position: relative;
}
@media (max-width: 1023px) {
  .blog li:nth-child(3n+3) {
    grid-row: auto;
    grid-column: auto;
  }
}

.blog article {
  width: 100%;
  height: 100%;
  display: flex;
}
@media (max-width: 1023px) {
  .blog article {
    min-height: 300px;
    display: grid;
    grid-template-columns: 49.93% 1fr;
  }
}
@media (max-width: 767px) {
  .blog article {
    width: 290px;
    height: auto;
    min-height: 256px;
    grid-template-columns: 1fr;
  }
}

.blog picture {
  width: 100%;
  max-width: 285px;
  height: auto;
}
@media (max-width: 1023px) {
  .blog picture {
    min-width: 339px;
    width: 100%;
    max-width: none;
    min-height: 300px;
  }
}
@media (max-width: 767px) {
  .blog picture {
    width: 290px;
    min-width: auto;
    height: 256px;
    min-height: auto;
  }
}

.blog img {
  width: 100%;
  height: 100%;
}
@media (max-width: 1023px) {
  .blog img {
    min-width: 339px;
    max-width: 100%;
    min-height: 300px;
  }
}
@media (max-width: 767px) {
  .blog img {
    width: 290px;
    min-width: auto;
    height: 256px;
  }
}

.blog__article-info {
  width: 285px;
  text-align: center;
  display: flex;
  flex-flow: column;
  align-items: center;
  background: #F0EAE4;
  padding: 66px 20px 12px;
}
@media (max-width: 1023px) {
  .blog__article-info {
    width: 100%;
    height: auto;
  }
}
@media (max-width: 767px) {
  .blog__article-info {
    width: 290px;
    height: auto;
    min-height: 256px;
    padding: 44px 0 0;
  }
}

.blog__article-info p {
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.01em;
  margin: 0 0 11px;
}
@media (max-width: 1023px) {
  .blog__article-info p {
    color: #9B9997;
  }
}

.blog__article-info h3 {
  font-family: "CormorantGaramond", "Arial", sans-serif;
  font-size: 26px;
  line-height: 33.8px;
  font-weight: 600;
  margin: 0 0 36px;
}
@media (max-width: 1023px) {
  .blog__article-info h3 {
    color: #000000;
    margin: 0 0 36px;
  }
}
@media (max-width: 767px) {
  .blog__article-info h3 {
    line-height: 34px;
  }
}

.blog__article-info a {
  font-weight: 500;
}

.blog__article-trends {
  position: relative;
}

.blog__article-trends-info {
  min-height: 301px;
  position: absolute;
  right: 0;
  bottom: 0;
}
@media (max-width: 1023px) {
  .blog__article-trends-info {
    min-height: 300px;
    position: initial;
    padding: 82px 0 0;
    order: 2;
  }
}
@media (max-width: 767px) {
  .blog__article-trends-info {
    min-height: 258px;
    padding: 62px 0 0;
  }
}

@media (max-width: 1023px) {
  .blog__article-trends h3 {
    margin: 0 0 36px;
  }
}
@media (max-width: 767px) {
  .blog__article-trends h3 {
    margin: 0 0 34px;
  }
}

.blog__article-trends picture {
  width: 100%;
  max-width: 570px;
  height: auto;
}
@media (max-width: 1023px) {
  .blog__article-trends picture {
