.filter__form {
  width: 21.37%;
  position: relative;
  padding: 0;
}

.filter__form-button-close {
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -16px;
  right: -16px;
  border-radius: 50%;
  background: #1D1613;
}

.filter__form-button-close svg {
  width: 14px;
  height: 14px;
}

.filter__form-button-close path {
  fill: white;
}

.filter__form fieldset {
  border: 1px solid #A87B62;
  padding: 0;
  margin: 0;
}

.filter__form-fieldset-wrapper {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  padding: 23px 23px 28px 25px;
}
@media (max-width: 1023px) {
  .filter__form-fieldset-wrapper {
    padding: 43px 50px 43px 49px;
  }
}
@media (max-width: 767px) {
  .filter__form-fieldset-wrapper {
    min-height: auto;
    padding: 17px 24px;
  }
}

.filter__button-container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.filter__button-container span {
  font-size: 16px;
  line-height: 20.8px;
}

.filter__input-container {
  width: 100%;
  display: flex;
  flex-flow: column;
  gap: 12px 0;
  padding: 23px 9px 0;
}

.filter__input-container.hide {
  display: none;
}

.filter__input-container label {
  width: 100%;
  height: max-content;
  text-align: left;
  display: flex;
}

.filter__input-container input {
  appearance: none;
}

.filter__input-container span {
  width: 100%;
  height: max-content;
  font-size: 14px;
  line-height: 18.2px;
  font-weight: 500;
  text-align: left;
  position: relative;
  padding: 0 0 0 25px;
}
.filter__input-container span::before {
  content: "";
  width: 15px;
  height: 15px;
  position: absolute;
  top: 2px;
  left: 0;
  border: 1px solid #A87B62;
  box-sizing: border-box;
}

.filter__input-container input:checked + span::after {
  content: "";
  width: 15px;
  height: 15px;
  position: absolute;
  top: 2px;
  left: 0;
  background: #A87B62;
  box-sizing: border-box;
}

.filter__form-fieldset-material-wrapper,
.filter__form-fieldset-collection-wrapper,
.filter__form-fieldset-price-wrapper {
  padding: 23px 22px 20px 23px;
}

.filter__form-fieldset-price-wrapper .filter__button-container {
  margin: 0 0 4px;
}

.filter-bar {
  width: 100%;
  height: 1px;
  position: relative;
  background: #F0EAE4;
}
.filter-bar::before {
  content: "";
  width: 15px;
  height: 15px;
  position: absolute;
  top: -7px;
  left: 19.9%;
  border-radius: 50%;
  background: #A87B62;
}
.filter-bar::after {
  content: "";
  width: 15px;
  height: 15px;
  position: absolute;
  top: -7px;
  left: 76.7%;
  border-radius: 50%;
  background: #A87B62;
}

.filter-bar .filter-bar-main {
  width: 50%;
  height: 1px;
  position: absolute;
  top: 0;
  left: 28%;
  background: #A87B62;
}

.filter-bar-price-container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 9% 0 16%;
}

.filter-bar-price-container p {
  width: max-content;
}

.filter-bar-price-container p + p {
  margin: 0;
}

.form__submit,
.form__reset {
  width: 100%;
  font-family: inherit;
  font-size: 16px;
  line-height: 20.8px;
  font-weight: 500;
  text-align: center;
  background: #A87B62;
  border: 1px solid #A87B62;
  padding: 25px 0 19px;
  margin: 34px 0 0;
  cursor: pointer;
}

.form__reset {
  background: transparent;
  padding: 23px 0 21px;
  margin: 27px 0 0;
}

.modal-filter {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #1D1613;
}

.modal-filter.hide {
  display: none;
}

.modal-filter__form {
  width: 442px;
  background: white;
}
@media (max-width: 767px) {
  .modal-filter__form {
    min-width: 270px;
  }
}